export class DialogueSystem {
  private isDialogueActive: boolean = false;
  private currentDialogue: string = '';
  private dialogueTimer: number = 0;
  private dialogueDuration: number = 3000; // 3 seconds

  public showDialogue(message: string): void {
    this.currentDialogue = message;
    this.isDialogueActive = true;
    this.dialogueTimer = 0;
  }

  public update(deltaTime: number): void {
    if (this.isDialogueActive) {
      this.dialogueTimer += deltaTime;
      if (this.dialogueTimer >= this.dialogueDuration) {
        this.hideDialogue();
      }
    }
  }

  public hideDialogue(): void {
    this.isDialogueActive = false;
    this.currentDialogue = '';
    this.dialogueTimer = 0;
  }

  public render(ctx: CanvasRenderingContext2D): void {
    if (!this.isDialogueActive || !this.currentDialogue) return;

    // Save context
    ctx.save();

    // Dialogue box background
    const boxWidth = Math.min(600, this.currentDialogue.length * 8 + 40);
    const boxHeight = 80;
    const boxX = (800 - boxWidth) / 2; // Center horizontally
    const boxY = 500; // Near bottom of screen

    // Background
    ctx.fillStyle = 'rgba(0, 0, 0, 0.8)';
    ctx.fillRect(boxX, boxY, boxWidth, boxHeight);

    // Border
    ctx.strokeStyle = '#FFFFFF';
    ctx.lineWidth = 2;
    ctx.strokeRect(boxX, boxY, boxWidth, boxHeight);

    // Speaker label
    ctx.fillStyle = '#FFD700'; // Gold color
    ctx.font = 'bold 14px sans-serif';
    ctx.textAlign = 'left';
    ctx.fillText('Bartender Jake:', boxX + 10, boxY + 20);

    // Dialogue text
    ctx.fillStyle = '#FFFFFF';
    ctx.font = '12px sans-serif';
    
    // Word wrap the dialogue
    const words = this.currentDialogue.split(' ');
    const maxWidth = boxWidth - 20;
    let line = '';
    let y = boxY + 40;

    for (let i = 0; i < words.length; i++) {
      const testLine = line + words[i] + ' ';
      const metrics = ctx.measureText(testLine);
      
      if (metrics.width > maxWidth && i > 0) {
        ctx.fillText(line, boxX + 10, y);
        line = words[i] + ' ';
        y += 16;
      } else {
        line = testLine;
      }
    }
    ctx.fillText(line, boxX + 10, y);

    // Interaction hint
    ctx.fillStyle = '#CCCCCC';
    ctx.font = '10px sans-serif';
    ctx.textAlign = 'right';
    ctx.fillText('Press SPACE to close', boxX + boxWidth - 10, boxY + boxHeight - 10);

    // Restore context
    ctx.restore();
  }

  public isActive(): boolean {
    return this.isDialogueActive;
  }
}
